import React, { Component } from 'react'
import Item from './Item'

export class List extends Component {
    state = {
        items : [
            { id: 1, title: "first" },
            { id: 2, title: "secound" },
            { id: 3, title: "third" },
            { id: 4, title: "fourth" },
            { id: 5, title: "fith" }
        ],
        last : null
    }
    handleUp = (index) => {
        if(index === 0){ }
        else {
            const clone = this.state.items;

            const first = clone[index]
            const secound = clone[index-1]

            clone[index-1] = first
            clone[index] = secound
            this.setState({items: clone})
        }
    }
    handleDown = (index) => {
        const last = this.state.items.length
        console.log(last, index+1)
        if(index + 1 === last){ }
        else {
            const clone = this.state.items;

            const first = clone[index]
            const secound = clone[index+1]

            clone[index+1] = first
            clone[index] = secound
            this.setState({items: clone})
        }
    }
    handleDelete = (index) => {
        console.log("delete", index)

        const clone = this.state.items
        clone.splice(index, 1)

        this.setState({items: clone})
    }
    componentDidMount() {
        this.setState({last : this.state.items.length})
    }
    
    render() {
        const {items} = this.state;

        return (
            <div>
                <div className="list">
                    { items.map(item => 
                        <Item 
                            key={item.id} 
                            index={items.findIndex(x => x.id === item.id)} 
                            id={item.id} 
                            up={this.handleUp} 
                            down={this.handleDown} 
                            title={item.title} 
                            last={this.state.last}
                            del={this.handleDelete}
                        />
                    ) }
                </div>
                <div className="edit">
                    <input type="text" />
                    <button>edit</button>
                </div>
            </div>
        )
    }
}

export default List
