import React, {useEffect, useState} from 'react';
import { motion } from 'framer-motion'

function Item({last, down, up, id, title, index, del}) {
    const [color, setColor] = useState("rgb(135, 73, 192)")
    // const [one, setOne] = useState(true)
    
    useEffect((one) => {
        if(one){ }
        else{
            setTimeout(() => {
                setColor("rgb(212, 66, 9)")
            }, 0);
            setTimeout(() => {
                setColor("rgb(135, 73, 192)")
            }, 250);
        }
    },[index])
    
    function shake(){
        console.log("shake")
    }
    function click(){
        console.log(index, id)
    }
    return (
        <motion.div initial={{ transform: "translate(-50%,-50%)" }} animate={{ top: `${index * 55}px`, backgroundColor: color }} className="item">
            <div className="left side">
                <button disabled={ !index } onClick={ index === 0 ? ()=>shake() : ()=>up(index)}>up</button>
                <button  disabled={ index + 1 === last } onClick={ ()=>down(index) }>down</button>
            </div>
            <div onClick={click} className="center">
                {title} - {id}- {id * 50}
            </div>
            <div className="right side">
                <button>edit</button>
                <button onClick={() => del(index)}>delete</button>
            </div>
        </motion.div>
    )
}

export default Item
